import { Axios } from 'axios'
import { ApiEntity, stringify, uriFy } from '../../core'
import {
  Signature,
  SignatureCheckBody,
  SignatureData,
  SignatureRoutes,
  SignaturesClientModel,
  SignatureSignBody,
  SignaturesSearchQuery,
} from '../signature.model'
import { WalletSignature } from '../../wallets'

export class SignaturesClient
  extends ApiEntity
  implements SignaturesClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a signature
   * @param body - the signature data
   * @returns the promise for create a new signature
   */
  public create(body: SignatureData) {
    return this._create<Signature>(SignatureRoutes.v1Create, body)
  }

  /**
   * Search signatures by params
   * @param searchParams - the signatures search params
   * @returns the promise for search signatures
   */
  public search(searchParams?: SignaturesSearchQuery) {
    return this._search<Signature>(SignatureRoutes.v1Search, searchParams)
  }

  /**
   * Search all signatures by params
   * @param searchParams - the signatures search params
   * @returns the promise for search all signatures
   */
  public searchAll(searchParams?: SignaturesSearchQuery) {
    return this._searchAll<Signature>(SignatureRoutes.v1Search, searchParams)
  }

  /**
   * List signatures by params
   * @param searchParams - the signatures search params
   * @returns the promise for list signatures
   */
  public list(searchParams?: SignaturesSearchQuery) {
    return this._list<Signature>(SignatureRoutes.v1Search, searchParams)
  }

  /**
   * List all signatures by params
   * @param searchParams - the signatures search params
   * @returns the promise for list all signatures
   */
  public listAll(searchParams?: SignaturesSearchQuery) {
    return this._listAll<Signature>(SignatureRoutes.v1Search, searchParams)
  }

  /**
   * Find a signature by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public findOne(searchParams?: SignaturesSearchQuery) {
    return this._findOne<Signature>(SignatureRoutes.v1Search, searchParams)
  }

  /**
   * Read a signature by params
   * @param searchParams - the search params
   * @returns the promise for read a signature
   */
  public readOne(searchParams?: SignaturesSearchQuery) {
    return this._readOne<Signature>(SignatureRoutes.v1Read, searchParams)
  }

  /**
   * Check signature by wallet
   * @param signatureId - the signature ID
   * @param params - the signature check body
   * @returns the promise for check signature
   */
  public check(signatureId: string, params: SignatureCheckBody) {
    return this.axios
      .request<WalletSignature>({
        url: SignatureRoutes.v1Check.replace(':signatureId', signatureId),
        method: 'GET',
        responseType: 'json',
        params: new URLSearchParams(uriFy(params)),
      })
      .then((response) => response.data)
  }

  /**
   * Sign a signature by wallet
   * @param signatureId - the signature ID
   * @param params - the signature sign body
   * @returns the promise for sign a signature
   */
  public sign(signatureId: string, body: SignatureSignBody) {
    return this.axios
      .request<WalletSignature>({
        url: SignatureRoutes.v1Sign.replace(':signatureId', signatureId),
        method: 'POST',
        responseType: 'json',
        data: stringify(body),
      })
      .then((response) => response.data)
  }
}
